@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&family=Roboto:ital,wght@0,300;0,400;0,500;0,600;1,300;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button {
    font-size: 1.3rem;
    padding: 1rem;
    color: #f0f0f0;
    background-color: var(--button-purple);
    transition-property: color, background-color, box-shadow;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
    border-radius: 0.2rem;
    box-shadow: 1px 1px 4px 2px rgba(0, 0, 0, 0.2);
}

.button:hover {
    transition: all 0.25s ease-in-out;
    color: white;
    background-color: var(--button-purple-hover);
    cursor: pointer;
}

.button:focus {
    outline: none;
}

.button:active {
    background: radial-gradient(circle, var(--light-purple), var(--button-purple-hover));
}

.button:disabled, .button:disabled:hover, .button:disabled:active, .button:disabled:active {
    background: rgba(83, 100, 125, 0.72);
    cursor: unset;
}

.button.button_secondary,
.button.button_secondary.button_disabled:hover,
.button.button_secondary.button_disabled:focus,
.button.button_secondary.button_disabled:active {
    background-color: var(--bluish-white);
    background: var(--bluish-white);
    border: 2px solid var(--button-purple);
    box-shadow: unset;
}

.button.button_secondary:hover {
    background-color: var(--light-purple);
}

.button.button_secondary:active {
    background: radial-gradient(circle, var(--bluish-white), var(--light-purple));
}

.card_container {
    background-color: white;
    box-shadow: 1px 1px 3px 2px rgba(0, 0, 0, 0.18);
    margin: 2rem;
    border-radius: 0.5rem;
}

.heading {
    font-family: Montserrat, Arial, sans-serif;
    text-transform: uppercase;
    color: var(--main-heading-purple);
    font-size: 4rem;
    font-weight: 500;
    text-align: center;
}

.subheading {
    font-family: Montserrat, Arial, sans-serif;
    color: var(--sub-heading-purple);
    font-weight: normal;
    font-size: 2rem;
}

.subsection {
    margin-top: 1.2rem;
}

.tooltip_container {
    position: relative;
}

.tooltip_container {
    height: 0;
    width: 0;
}

[tooltip]:after {
    cursor: default;
    position: absolute;
    left: 2.7rem;
    top: 0;
    content: attr(tooltip);
    opacity: 0;
    color: var(--text-purple);
    background-color: var(--tooltip-orange);
    font-size: 1rem;
    font-weight: normal;
    padding: 0.25rem;
    line-height: 1rem;
    border: 1px dashed var(--night-purple);
    transition: all ease-in-out 0.2s;
    transition-delay: 0.2s;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
}

[tooltip]:hover:after {
    cursor: default;
    opacity: 1;
}

.error_message {
    display: inline-block;
    position: absolute;
    left: 0;
    bottom: -0.8rem;
    opacity: 0;
    color: black;
    font-size: 1rem;
    background-color: #ffe100;
    padding: 0.2rem 0.4rem;
    margin-top: 0;
    border-radius: 0.1rem;
    transition: all 0.2s ease-in-out;
}

.error_message.generic_error {
    position: static;
    margin-top: 1rem;
    padding: 0.4rem;
    text-align: center;
}

.error_visible {
    opacity: 1;
    transition: all 0.2s ease-in-out;
}

.error_hidden {
    opacity: 0;
    transition: all 0.2s ease-in-out;
}

.error_highlight {
    background: rgba(224, 7, 7, 0.13);
}

.hover_text {
    background-color: var(--button-purple);
    color: white;
    /*opacity: 0;*/
    position: absolute;
    cursor: pointer;
    transition: all ease-in-out 0.2s;
    font-size: 1.2rem;
    padding: 0.2rem 0.5rem;
    box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.15);
}

.hover_text:hover {
    background-color: var(--button-purple-hover);
    opacity: 1;
}

.button_danger.button {
    color: #f0f0f0;
    transition: all 0.1s ease-in-out;
    background-color: var(--main-red);
}

.button_danger.button:hover {
    transition: all 0.1s ease-in-out;
    color: white;
    background-color: var(--light-red);
}

.button_danger.button:active {
    transition: all 0.1s ease-in-out;
    background: radial-gradient(circle, var(--light-red), var(--main-red));
}

.button.button_disabled:hover,
.button.button_disabled:active,
.button.button_disabled:focus {
    cursor: unset;
}

.button.button_disabled {
    opacity: 0.9;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.1);
}

.button.button_yellow {
    background: var(--button-color-alt);
    color: var(--text-purple-dark);
}

.button.button_green {
    background: var(--main-green);
}

.button.button_green:hover {
    background: var(--main-green-light);
}

.button.button_green:active {
    background: radial-gradient(circle, var(--main-green-white), var(--main-green-light));
}

.button.button_blue_pressed {
    background: radial-gradient(circle, var(--button-blue-white), var(--button-blue)) !important;
    box-shadow: 1px 1px 3px 2px rgba(10, 10, 10, 0.3) !important;
}

.button.button_blue_pressed:hover {
    background: radial-gradient(circle, var(--button-blue-white), var(--button-blue-hover)) !important;
}

.button.button_blue_pressed:active {
    background: radial-gradient(circle, var(--button-blue), var(--button-blue-white)) !important;
}

.button.button_yellow_pressed {
    background: radial-gradient(circle, var(--button-color-alt-white), var(--button-color-alt-light));
}

.button.button_yellow_pressed:hover {
    background: radial-gradient(circle, var(--button-color-alt-white), var(--button-color-alt)) !important;
}

.button.button_yellow_pressed:active {
    background: radial-gradient(circle, var(--button-color-alt-white), var(--button-color-alt)) !important;
}

.button.button_yellow:hover {
    background: var(--button-color-alt-light);
}

.button.button_yellow:active {
    background: radial-gradient(circle, var(--button-color-alt-white), var(--button-color-alt-light));
}

.button.button_blue {
    background: var(--button-blue);
}

.button.button_blue:hover {
    background: var(--button-blue-hover);
}

.button.button_blue:active {
    background: radial-gradient(circle, var(--button-blue-white), var(--button-blue-hover));
}

.button.button_yellow.button_disabled:hover,
.button.button_yellow.button_disabled:active,
.button.button_yellow.button_disabled:focus {
    background: var(--button-color-alt);
}

.button.button_purple_border {
    border: 0.3rem solid var(--button-purple);
}

.button.button_purple_border:hover {
    border: 0.3rem solid var(--button-purple-hover);
}

.hide_me {
    opacity: 0;
    height: 0;
    visibility: hidden;
    display: none;
}

.show_me {
    opacity: 100;
    visibility: visible;
    display: unset;
}

.slide_in {
    -webkit-animation: slideIn 0.5s forwards cubic-bezier(0.2, 1, 0.3, 1);
            animation: slideIn 0.5s forwards cubic-bezier(0.2, 1, 0.3, 1);
}

.slide_in_icon {
    -webkit-animation: slideInIcon 0.5s forwards cubic-bezier(0.2, 1, 0.3, 1);
            animation: slideInIcon 0.5s forwards cubic-bezier(0.2, 1, 0.3, 1);
}

.help_para {
    margin-bottom: 1rem;
}

.help_heading {
    font-weight: 600;
    color: var(--main-green);
}

@-webkit-keyframes slideInIcon {
    0% {
        opacity: 0;
        visibility: hidden;
        height: 0;
        -webkit-transform: translateY(-5rem);
                transform: translateY(-5rem);
    }
    100% {
        opacity: 1;
        visibility: visible;
        display: unset;
        height: 100%;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slideInIcon {
    0% {
        opacity: 0;
        visibility: hidden;
        height: 0;
        -webkit-transform: translateY(-5rem);
                transform: translateY(-5rem);
    }
    100% {
        opacity: 1;
        visibility: visible;
        display: unset;
        height: 100%;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes slideIn {
    0% {
        opacity: 0;
        visibility: hidden;
        height: 0;
        -webkit-transform: translateY(-15rem);
                transform: translateY(-15rem);
    }

    25% {
        opacity: 1;
        visibility: visible;
        display: unset;
        height: 100%;
        -webkit-transform: translateY(-12rem);
                transform: translateY(-12rem);
    }

    100% {
        opacity: 1;
        visibility: visible;
        display: unset;
        height: 100%;
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
    }
}

@keyframes slideIn {
    0% {
        opacity: 0;
        visibility: hidden;
        height: 0;
        -webkit-transform: translateY(-15rem);
                transform: translateY(-15rem);
    }

    25% {
        opacity: 1;
        visibility: visible;
        display: unset;
        height: 100%;
        -webkit-transform: translateY(-12rem);
                transform: translateY(-12rem);
    }

    100% {
        opacity: 1;
        visibility: visible;
        display: unset;
        height: 100%;
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
    }
}

:root {
    --night-purple: rgb(11, 5, 47);
    --light-purple: rgb(182, 180, 255);
    --white-blue: rgb(255, 250, 250);
    --bluish-white: rgb(250, 250, 255);
    --sub-heading-purple: rgb(53, 37, 110);
    --main-heading-purple: rgba(72, 5, 140, 1);
    --text-purple: rgb(66, 20, 100);
    --text-purple-dark: rgb(26, 5, 42);
    --button-purple: rgb(89, 65, 209);
    --button-purple-hover: rgb(97, 95, 245);
    --text-alternate: rgba(245, 117, 0, 1);

    --text-red: rgb(147, 5, 5);
    --main-red: rgb(182, 3, 37);
    --main-red2: rgb(185, 35, 2);

    --light-red: rgb(231, 41, 77);
    --yellow: rgba(255, 225, 0, 1);
    --yellow-green: rgb(165, 187, 52);
    --tooltip-orange: rgba(255, 226, 35, 0.93);
    --button-color-alt: rgba(254, 228, 7, 1);
    --button-color-alt-light: rgba(255, 248, 67, 1);
    --button-color-alt-white: rgb(255, 253, 249);
    --button-blue: rgb(29, 133, 229);
    --button-blue-hover: rgb(55, 158, 255);
    --button-blue-white: rgb(162, 210, 255);
    --blue_light: rgb(54, 138, 222);

    --yes-background: rgba(216, 226, 252, 0.53);
    --no-background: rgba(253, 226, 217, 0.66);
    --main-green: rgba(0, 157, 44, 1);
    --main-green-light: rgb(31, 200, 78);
    --main-green-white: rgb(177, 239, 194);
    --logo-green: rgba(35, 156, 47, 1);

}

html {
    scroll-behavior: smooth;
}

* {
    font-family: Roboto, Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border: none;
    color: rgb(66, 20, 100);
    color: var(--text-purple);
}

body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: url(/game/static/media/background.333767be.svg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ol, ul, ul li, ol li {
    list-style: none;
}

a, a:link, a:hover, a:visited {
    text-decoration: none;
}

input {
    padding: 0.6rem;
    background-color: rgb(255, 250, 250);
    background-color: var(--white-blue);
    border: rgb(215, 224, 230) solid 2px;
    border-radius: 0.3rem;
}

input:focus {
    transition: all 0.25s ease-in-out;
    border: rgb(45, 100, 180) solid 2px;
    outline: none;
}

h1, h2, h3, h4 {
    font-weight: unset;
    color: rgb(66, 20, 100);
    color: var(--text-purple);
}

.network_error_container {
    display: flex;
    max-width: 40rem;
    margin: 4rem auto 0 auto;
    flex-direction: column;
    align-items: center;
    background: white;
    padding: 3rem;
    border-radius: 1rem;
    box-shadow: 2px 2px 4px 2px rgba(50, 50, 50, 0.35);
    transition: all ease-in-out 0.2s 0.5s;
    -webkit-transform: translateY(10rem);
            transform: translateY(10rem);
}

.network_error_container .error_message {
    opacity: 1;
    position: unset;
    color: rgb(66, 20, 100);
    color: var(--text-purple);
    font-size: 1.5rem;
    line-height: 1.5;
    padding: 1rem;
    border-radius: 1rem;
    box-shadow: 0 0 1px 1px rgba(50, 50, 50, 0.15);
}

.network_error_container .button {
    margin: 4rem auto 0 auto;
    border-radius: 3rem;
    padding: 1.5rem 2rem;
    box-shadow: 1px 1px 3px 2px rgba(50, 50, 50, 0.2);
}

.intelligeo_logo {
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-size: 2rem;
    font-weight: 500;
}

.intelligeo_logo .intelli {
    color: #500389;
    padding: 0 0.1rem 0.1rem 0.1rem;
}

.intelligeo_logo .geo {
    background-color: #DE6A01;
    color: white;
    padding: 0 0.9rem 0.1rem 0.9rem;
    border-radius: 3rem;
}

.app_content {
    min-height: 90vh;
}

.main {
    margin: 0 1rem;
}

.loading_screen_container {
    margin: 10rem auto;
    width: 35rem;
    padding: 6rem 2rem;
    text-align: center;
    background: white;
    border-radius: 1rem;
    box-shadow: 2px 2px 5px 2px rgba(50, 50, 50, 0.15);
    font-size: 2rem;
}

.loading_screen_container span {
    display: inline-block;
    margin-bottom: 1rem;
}

.everything_except_footer {
    min-height: 91.2vh;
}
@media (max-width: 660px) {
    .network_error_container .error_message {
        font-size: 1.3rem;
    }
    .network_error_container {
        margin: 0 1rem;
        padding: 2rem;
    }
}

@media (max-width: 570px) {
    .network_error_container .error_message {
        font-size: 1.2rem;
    }
}

@media (max-width: 470px) {
    .network_error_container .button {
        font-size: 1.2rem;
        padding: 1rem 1.5rem;
        margin-top: 3rem;
    }
    .network_error_container .error_message {
        font-size: 1rem;
    }
}

.footer.outer_container {
    margin: 3rem 0 0 0;
    width: 100%;
    background: linear-gradient(92.35deg, rgba(242, 167, 26, 0.83) -46.22%, rgba(242, 187, 22, 0.84) 2.17%, rgba(254, 228, 7, 0.86) 98.92%);
    -webkit-backdrop-filter: blur(6px);
            backdrop-filter: blur(6px);
}

.footer_content {
    padding: 1.2rem 0 1rem 0;
}

.copyright {
    color: var(--night-purple);
    text-align: center;
    font-size: 0.8rem;
}


.header.outer_container {
    margin: 0;
    width: 100%;
    top: 0;
    position: fixed;
    z-index: 3;
    background: linear-gradient(22.15deg, rgba(134, 45, 219, 0.9) 6.88%, rgba(52, 11, 199, 0.83) 52.91%, rgba(134, 45, 219, 0.81) 96.88%);
    padding: 1.3rem;
    -webkit-backdrop-filter: blur(6px);
            backdrop-filter: blur(6px);
    box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.2);
}

.header_content {
    padding: 1.2rem 0 1rem 0;
}

.intelligeo-logo {
    text-align: center;
    font-size: 2rem;
    font-weight: 500;
}

.intelligeo-logo a:hover {
